/*@import url('https://fonts.googleapis.com/css?family=Open+Sans');*/
@import url('https://fonts.googleapis.com/css?family=Quicksand:300,400,700');
body {
    font-family: "Quicksand", serif;
}

.wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    background: rgb(47, 203, 253);
    background: -moz-linear-gradient(135deg, rgb(47, 203, 253) 0%, rgba(96,40,168,1) 100%);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgb(47, 203, 253)), color-stop(100%, rgba(96,40,168,1)));
    background: -webkit-linear-gradient(135deg, rgb(47, 203, 253) 0%, rgba(96,40,168,1) 100%);
    background: -o-linear-gradient(135deg, rgb(47, 203, 253) 0%, rgba(96,40,168,1) 100%);
    background: -ms-linear-gradient(135deg, rgb(47, 203, 253) 0%, rgba(96,40,168,1) 100%);
    background: linear-gradient(135deg, rgb(47, 203, 253) 0%, rgba(96,40,168,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.main {
    height: 55vh;
    background: #2a6bb1;
    box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
    width: 60%;
    margin: 0 auto;
}

.info {
    height: 55vh;
    background-blend-mode: multiply;
    background: #484747 url("./img/polygon.jpg") no-repeat center center;
    /*border: 2px solid #1f1f1f;*/
    /*border-right: 2px solid #131e29;*/
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #000;
}

.form {
    height: 55vh;
    padding-top: 50px;
    padding-left: 50px;
    /*border-top: 2px solid #113a65;*/
    /*border-bottom: 2px solid #113a65;*/
    /*border-right: 2px solid #113a65;*/
}

input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 2px #113a65;
    width: 50%;
    padding-bottom: 4px;
    color: #fff;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 18px;
    outline: none;
    font-style: italic;
}

input::-webkit-input-placeholder {
    color: #103a65;
}

input::-moz-placeholder {
    color: #103a65;
}

button {
    border: 2px solid #103a65;
    border-bottom: 4px solid #103a65;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    cursor: pointer;
    background-color: #235d9c;
    color: #fff;
    outline: none!important;
}

button:hover {
    border-bottom: 2px solid #103a65;
    position: relative;
    top: 2px;
}

button:active {
    border-bottom: 2px solid transparent;
    border-color: transparent;
    position: relative;
    top: 2px;
}

.info h2 {
    font-size: 3em;
    color: #fff;
}

.info p {
    color: #e4e4e4;
    font-size: 18px;
    font-weight: bold;
}

.infoWeath {
    width: 60%;
    font-size: 18px;
    color: #fff;
}

.error {
    color: #973f32;
    font-weight: bold;
}